.Contact {
    padding: 50px 20px;
    background-color: #fff;
    margin: 20px auto;
    max-width: 800px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
}

.Contact h1 {
    font-size: 2em;
    margin-bottom: 20px;
}

.Contact h3 {
    font-size: 1.2em;
}

.Contact h3 p {
    font-size: 1.2em;
    color: #007BFF;
}

.Contact p {
    font-size: 1.2em;
}
