.about {
    padding: 50px 20px;
    background-color: #fff;
    margin: 20px auto;
    max-width: 800px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
}

.about h1 {
    color: #333;
    margin-bottom: 20px;
    font-size: 2em;
}

.about p {
    font-size: 1.2em;
    line-height: 1.6;
}
