header {
    background-color: #333; 
    color: white;
    text-align: center;
    display: flex;
    justify-content: space-between; 
    align-items: center; 
    padding: 0 30px;
    position: sticky;
    top: 0; 
    z-index: 1000; 
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}


header h1 {
    font-size: 2.5em; 
    font-weight: bold;
    margin: 0;
    color: #ff9800; 
}


nav ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

nav ul li {
    display: inline-block;
    margin-right: 20px; 
}

nav ul li a {
    color: white;
    text-decoration: none;
    font-size: 1.2em;
    font-weight: 600;
    transition: color 0.3s ease;
}

nav ul li a:hover {
    color: #ff9800; 
    text-decoration: underline;
}

@media (max-width: 768px) {
    header {
        padding: 10px;
    }
    
    header h1 {
        font-size: 2em;
    }
    
    nav ul li {
        margin-right: 10px;
    }
    
    nav ul li a {
        font-size: 1em;
    }
}
