.projects {
    padding: 50px 20px;
    background-color: #fff;
    margin: 20px auto;
    max-width: 800px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
}

.projects h1 {
    color: #333;
    margin-bottom: 20px;
    font-size: 2em;
}

.projects ul {
    list-style-type: none;
    padding: 0;
}

.projects li {
    margin: 10px 0;
}

.projects li a {
    color: #007BFF;
    text-decoration: none;
    font-weight: bold;
}

.projects li a:hover {
    text-decoration: underline;
}
